body{
  margin: 0px;
  text-align: center;
  padding-top: 50px;
  background-color: rgb(61, 54, 54);
  display: flex;
  justify-content: center;
  height: calc(100vh - 50px);
  font-family: 'Rubik', sans-serif;
}
#root{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.Note{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:350px;
  max-width: 350px;
  height: auto;
  background-color: rgb(113, 107, 107);
  padding: 20px;
  position: relative;
}
.NoteHolder{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  flex-direction: column;
  margin-top: 5px;
  gap: 40px;
}
.Note div{
  display: flex;
  flex-direction: row;
}

.InputBox{
  width: 390px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;

}
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap');
.InputBox button{
  background: #f1f1f1;
  border: 0px;
  border-left: black solid 2px;
  height: 100%;
  padding: 15px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-family: 'Rubik', sans-serif;
}
.Input{
  width: 100%;
  padding: 15px;
  display: inline-block;
  border: none;
  background: #f1f1f1;
  font-family: 'Rubik', sans-serif;
  font-size: 25px;
}
input:focus {
  outline: none;
}



.Note p{
  height: 100%;
  padding: 0px;
  color: white;
  font-size: 35px;
  margin: 0px;
  font-family: 'Rubik', sans-serif;
  max-width: 100%;
  word-wrap: break-word;
}
.Note button{
  border: 0px;
  padding: 10px;
  font-size: 25px;
  border-radius: 50px;

}

.Delete{
  position: absolute;
  right: -30px;
  top: -30px;
  background-color: red;
  padding: 20px;
  cursor: pointer;
}


.Edit{
  position: absolute;
  right: 30px;
  top: -30px;
  background-color: rgb(66, 227, 101);
  padding: 20px;
  cursor: pointer;
}
